import { Component, h, Host, Prop } from '@stencil/core'

@Component({
  tag: 'ato-col',
  shadow: false,
})
export class AtoCol {
  @Prop() size?: 'auto' | string
  @Prop() offset?: string
  @Prop() offsetSm?: string
  @Prop() offsetMd?: string
  @Prop() offsetLg?: string
  @Prop() push?: string
  @Prop() pull?: string

  render() {
    return (
      <Host>
        <ion-col
          size={this.size}
          offset={this.offset}
          offsetSm={this.offset}
          offsetMd={this.offset}
          offsetLg={this.offset}
          push={this.push}
          pull={this.pull}
        >
          <slot />
        </ion-col>
      </Host>
    )
  }
}
